.gk-map {
  div {
    margin: 0 auto;
    max-width: 1950px;

    & iframe {
      height: 600px;
      max-width: 1950px;
    }
  }

  & h2 {
    margin: 0 15px;

  @media (min-width: @tablet-width) {
    margin: 0 40px;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
    width: 1100px;
    margin: 0 auto;
  }
  }
}
